body{
  font-family: 'Dosis', sans-serif;
  background-color: #d5c9f1;
animation-name: bg;
animation-duration: 7s;
animation-iteration-count: infinite;
}
@keyframes bg{
  0% {background-color: #d5c9f1;}
  20% {background-color: #d1abf9;}
  40% {background-color: #c696ff;}
  60% {background-color: #c296f4;}
  80% {background-color: #bb9ffc;}
  100% {background-color: #d5c9f1;}
}
.btn-color{
  background-color: #9f7fec;
}
.img-tran{
  transform: none;
  transition: transform 2s;
  transition-timing-function: ease-out
}
.img-tran:hover{
transform: rotateX(160deg);
}

.my-icon{
  color: #9f7fec;
  text-align: none;
  animation-name: example;
  animation-duration: 3s;
  text-align: center;
  animation-iteration-count: infinite;
}
@keyframes example {
from{color: #9f7fec;}
to{color: black;}
}
.img-tran2{
  transform: none;
  transition: transform 2s;
  transition-timing-function: ease-out
}
.img-tran2:hover{
  transform: rotateY(160deg);
  }
  .text-c{
    text-align: center;
  }
  .text-size-card{
    font-size: 20px;
  }
  #displayset{
   background-color: none;
  }
  #createvis{
    visibility: hidden;
  }
  .img-width{
    width: 25rem;
  }
  .anime-text::before{
    content: "";
    animation-name: textchange;
    animation-duration: 6s;
    animation-iteration-count: infinite;
  }
  @keyframes textchange{
    0% {content: "";}
    20% {content: "Z";}
    30%{content: "Ze";}
    40% {content: "Zet";}
    60% {content: "Zeth";}
    80% {content: "Zethe";}
    90%{content: 'Zethed';}
    100%{content: 'Zethedu';}
    
  }